.Header {
  font-weight: bold;
  font-size: 28px;
  text-align: center;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: var(--blue-color);
  margin-top: 10px;
  margin-bottom: 10px;
  align-self: center;
}

.HeaderNotFound {
  background: transparent;
  font-size: 30px;
  color: var(--blue-color);
}
